import { Modal } from "antd"
import { collection, doc, getDoc, getDocs, query, setDoc, updateDoc, where } from "firebase/firestore"
import { useEffect, useState } from "react"
import SignatureCanvas from 'react-signature-canvas'
import { toast } from "react-toastify"
import Button from "../../../../components/Button"
import Loading from "../../../../components/shared/Loading"
import { auth, db } from "../../../../configs/firebase.config"
import { useAppDispatch, useAppSelector } from "../../../../hooks/useRedux"
import { authUpdateSsignature, confirmLoan } from "../../../../redux/reducers/auth"
import { random } from "../../../../service/radom"
import Contract from "./Contract"

export default function ConfirmDebit() {

    const [account, setAccount] = useState<any>(null)
    const uid = useAppSelector(state => state.auth.uid)
    const isLoading = useAppSelector(state => state.auth.isLoading)

    const [open, setOpen] = useState(false);
    const [isCheckContract, setIsCheckContract] = useState<boolean>(false)

    const infoLoan = useAppSelector(state => state.auth.loan)
    const [sign, setSign] = useState<any>()

    const user = auth.currentUser;

    const dispatch = useAppDispatch()

    const borrowedTime = () => {
        const date = new Date()
        const day = date.getDate()
        const month = date.getMonth() + 1
        const year = date.getFullYear()

        // HOUR
        const hour = date.getHours()
        const minute = date.getMinutes()
        const second = date.getSeconds()

        return `${day}/${month}/${year} ${hour}:${minute}:${second}`
    }

    const formatMoney = (number: number) => {
        number = Math.round(number * 1000) / 1000;

        return number
            .toFixed(0)
            .replace(/\D/g, "")
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    const handleConfirmSign = () => {
        if (sign.isEmpty()) {
            toast.error('Vui lòng kí vào khung trống')
        } else {
            sign.getTrimmedCanvas().toDataURL('image/png')
            setSign(sign.getTrimmedCanvas().toDataURL('image/png'))
            dispatch(authUpdateSsignature(sign.getTrimmedCanvas().toDataURL('image/png')))
        }
    }

    const getAccount = async () => {
        const docRef = doc(db, "users", uid);
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
            setAccount(docSnap.data());
        }
    }

    useEffect(() => {
        getAccount()
    }, [])

    const handleConfirmLoan = async () => {
        if (!isCheckContract) {
            toast.error('Vui lòng đọc và đồng ý với hợp đồng')
        } else {

            const loan = query(
                collection(db, 'loan-list'),
                where("uid", "==", uid)
            );

            const querySnapshot = await getDocs(loan);

            if (querySnapshot.size > 0) {
                toast.error('Đã tồn tại 1 hợp đồng vay, vui lòng liên hệ hỗ trợ!')
            } else {

                // get users by uid
                const docRef = doc(db, "users", uid);
                const docSnap = await getDoc(docRef);

                // update status user
                if (docSnap.exists()) {
                    await updateDoc(docRef, {
                        status: 'active',
                        idContract: random(),
                        surplus: infoLoan?.loanAmount,
                    })
                }


                const data = {
                    ...account,
                    loan: infoLoan,
                    status: 'pending',
                    uid: uid,
                    BorrowedTime: borrowedTime(),
                    idContract: random()
                }
                dispatch(confirmLoan(data))
            }
        }
    }

    if (!infoLoan) {
        return (
            <div></div>
        )
    }

    return (
        <div className='p-4 flex flex-col gap-4 items-center' style={{
          paddingBottom: 128
        }}>
            {
                isLoading && <Loading />
            }
            <Modal
                open={open}
                closeIcon={null}
                onOk={() => setOpen(false)}
                footer={[
                    <div key="print" className=" flex justify-end items-center gap-2 py-2 border-t  border-gray-200">
                        <Button className="py-2 px-4 rounded-lg bg-[#29badb] text-white text-sm" onClick={() => {
                            console.log('print');
                        }}>
                            In PDF
                        </Button>
                        <Button key="close" className="py-2 px-4 rounded-lg bg-[#3d78e3] text-white text-sm" onClick={() => setOpen(false)}>
                            Đóng
                        </Button>
                    </div>
                ]}
            >
                <Contract Amount={infoLoan.loanAmount} IdContract={(infoLoan as any)?.idContract} CCCD={account?.cmnd} Date={borrowedTime()} InterestRate={`${infoLoan.loanInterestRate}`} Time={`${infoLoan.loanMonth}`} />
            </Modal>
            <h1 className="font-bold text-lg">Xác nhận khoản vay  </h1>
            <div className="w-full">
                <p>Khoản tiền vay : <span className="font-bold"> {formatMoney(infoLoan.loanAmount)} VNĐ</span></p>
                <p>Thời hạn thanh toán : <span className="font-bold"> {infoLoan.loanMonth} tháng</span></p>
                <Button className="mt-4 w-full py-1 rounded-[3rem] font-semibold bg-[#ff7324] text-white duration-150 active:scale-90 hover:bg-white border border-transparent hover:border-primary hover:text-primary"
                    onClick={() => setOpen(true)}
                >
                    Xem hợp đồng
                </Button>
                <div className="flex items-center gap-2 mt-4">
                    <input type="checkbox" id="check" className="w-4 h-4" checked={isCheckContract} onChange={() => setIsCheckContract(!isCheckContract)} />
                    <label htmlFor="check">
                        Xác nhận đồng ý với hợp đồng
                    </label>
                </div>
            </div>
            <div className="w-full flex flex-col items-start">
                <p>
                    {!account?.signature ? (
                        <span className="text-red-500">Vui lòng kí vào khung trống</span>
                    ) : (
                        <span className="text-green-500">Kí thành công</span>
                    )}
                </p>
                {
                    account?.signature ? (
                        <div className="mt-4 border border-black">
                            <img src={account?.signature} alt="" />
                        </div>
                    ) : (
                        <>
                            <div className="border mt-4 w-full border-black">
                                <SignatureCanvas
                                    ref={(ref) => setSign(ref)}
                                    penColor='black'
                                    dotSize={5}
                                    velocityFilterWeight={0}
                                    canvasProps={{
                                        width: 500,
                                        height: 400,
                                        className: 'sigCanvas'
                                    }}
                                />
                            </div>
                            <Button className="underline"
                                onClick={() => sign.clear()}
                            >
                                Làm mới
                            </Button>
                        </>
                    )
                }
                <div className="w-full mt-8 flex justify-center">
                    {
                        account?.signature ? (
                            <Button kind="primary" className="font-medium py-2 px-4"
                                onClick={() => handleConfirmLoan()}
                            >
                                Xác nhận
                            </Button>
                        ) : (
                            <Button kind="primary" className="font-medium py-2 px-4"
                                onClick={() => handleConfirmSign()}
                            >
                                Xác nhận chữ ký
                            </Button>
                        )
                    }
                </div>
            </div>
        </div>
    )
}
